body * {
   margin: 0;
   padding: 0;
   outline: 0;
}

body {
   background: #fafafa;
   font: 14px Arial, Helvetica, sans-serif;
   -webkit-font-smoothing: antialiased !important;
}

button {
   padding: 5px;
   background: transparent;
   border: 1px solid rgba(0,0,0,0.5);
   border-radius: 5px; 
}

button:hover {
   background: rgba(0,0,0,0.5);
   color: white;
   border-radius: 5px; 
}

.container {
   display: flex;
   align-items: center;
   justify-content: center;
   flex-direction: column;
}

.title-box {
   justify-content: center;
   display: flex;
   font-size: 40px;
}

.show-important {
   margin: 5px;
}

.new-note {
   margin: 5px;
}

form input {
   padding: 5px;
   background: transparent;
   border: 1px solid rgba(0,0,0,0.7);
   border-radius: 5px; 
}

.note {
   border: 1px solid rgba(0,0,0,0.7);
   width: 100%;
   min-width: 600px;
   height: 50px;
   font-size: 15px;
   display: flex;
   justify-content: space-between;
   align-items: center;
   border-radius: 6px;
   margin: 5px;
   padding: 10px;
}

.note:hover {
   box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.12),
   0px 3px 1px -2px rgba(0, 0, 0, 0.12),
   0px 1px 5px 0px rgba(0,0,0,0.2);
} 

.note-box {
   flex-direction: column;
   list-style-type: none;
}